.number-plate-contents{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    width: 100%;
}

.number-button{
    width: 200px;
    height: 50px;
    
    border-radius: 15px;
    background-color: white;
    border: 2px solid steelblue;   
    color: steelblue;
    margin: auto 10px;
    font-size: 20px;
    outline: none;
    cursor: pointer;
}